'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { usePathname } from 'next/navigation'
import { motion, AnimatePresence } from 'framer-motion'

export default function Navbar() {
  const pathname = usePathname()
  const [open, setOpen] = useState(false)

  // Disable scroll when mobile menu is open
  useEffect(() => {
    document.body.style.overflow = open ? "hidden" : "auto"
  }, [open])

  const links = [
    { name: "Services", path: "/services" },
    { name: "Projects", path: "/projects" },
    { name: "About", path: "/about" },
  ]

  return (
    <motion.nav
      initial={{ y: -40, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.6 }}
      className="
        fixed top-0 left-0 w-full z-[50]
        backdrop-blur-xl bg-white/20
        border-b border-white/30
      "
    >
      <div className="container mx-auto px-6 h-20 flex items-center justify-between">

        {/* LOGO */}
        <Link href="/" className="text-xl font-bold tracking-wide text-white">
          Venturemond
        </Link>

        {/* DESKTOP MENU */}
        <div className="hidden md:flex items-center gap-10">
          {links.map(link => (
            <Link
              key={link.name}
              href={link.path}
              className={`
                text-white/90 font-medium hover:text-white transition
                relative group
              `}
            >
              {link.name}

              {/* Hover underline */}
              <span
                className={`
                  absolute left-0 -bottom-1 h-[2px] w-0 bg-gradient-to-r 
                  from-blue-500 to-purple-500 rounded-full
                  transition-all duration-300 group-hover:w-full
                `}
              ></span>

              {/* Active underline */}
              {pathname === link.path && (
                <span className="absolute left-0 -bottom-1 h-[2px] w-full bg-blue-400 rounded-full"></span>
              )}
            </Link>
          ))}

          {/* CONTACT BUTTON */}
          <Link
            href="#contact"
            className="
              px-5 py-2 rounded-lg font-medium text-white
              bg-gradient-to-r from-blue-500 to-purple-500
              hover:opacity-90 transition shadow-md
            "
          >
            Contact
          </Link>
        </div>

        {/* MOBILE TOGGLE */}
        <button
          className="md:hidden text-white text-3xl"
          onClick={() => setOpen(!open)}
        >
          â˜°
        </button>

      </div>

      {/* MOBILE MENU SLIDE DOWN */}
      <AnimatePresence>
        {open && (
          <motion.div
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            transition={{ duration: 0.2 }}
            className="
              md:hidden bg-white/20 backdrop-blur-xl border-t border-white/30 
              text-white px-6 py-4 space-y-4
            "
          >
            {links.map(link => (
              <Link
                key={link.name}
                href={link.path}
                onClick={() => setOpen(false)}
                className="block py-2 text-lg font-medium hover:text-white/80"
              >
                {link.name}
              </Link>
            ))}
            <Link
              href="#contact"
              onClick={() => setOpen(false)}
              className="block py-3 text-center mt-4 rounded-lg 
              bg-gradient-to-r from-blue-500 to-purple-500
              font-semibold shadow-md"
            >
              Contact
            </Link>
          </motion.div>
        )}
      </AnimatePresence>
    </motion.nav>
  )
}
